import { ElMessageBox } from "element-plus";
export function useXcxUsers() {
  const server = inject('server');
  const allTywUsers = ref([]);
  const allArticle = ref([]);
  const TywUserItem = ref();
  const articleItem = ref();
  var TywUserid = "";
  //
  const getTywUserDetail = async id => {
    // @ts-ignore
    const res = await server.userApi.getTywUsergoryDetail({
      "id": parseInt(id, 10)
    });
    if (res.code === 200) {
      TywUserItem.value = res.data;
    } else {
      ElMessage.error(res.msg);
      throw new Error(res.msg || '');
    }
    // console.log(res)
  };
  //
  const getAllTywUser = async () => {
    // @ts-ignore
    const res = await server.userApi.geAllTywUser();
    if (res.code === 200) {
      allTywUsers.value = res.data;
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
    // console.log(res)
  };
  //
  const deleteTywUser = async id => {
    // 1.
    await ElMessageBox.confirm("", "", {
      confirmButtonText: "",
      cancelButtonText: "",
      type: 'warning'
    }).catch(() => {
      ElMessage.info("");
      return new Promise(() => {});
    });
    // 2.
    // @ts-ignore
    const res = await server.userApi.postDetTywUser({
      "id": id
    });
    if (res.code === 200) {
      ElMessage.success("");
      getAllTywUser();
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
  };
  return {
    getAllTywUser,
    allTywUsers,
    deleteTywUser,
    getTywUserDetail,
    TywUserItem,
    allArticle
  };
}